.resume {
  text-align: left;
}

h1 {
  text-align: left;
}

.fa-file-pdf {
  font-size: x-large;
}

.form {
  text-align: left;
}

label {
  display: inline-block;
  width: 150px;
  text-align: right;
}

main {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
.about-pic {
  display: flex;
  padding: 5px;
  justify-content: center;
  align-items: center;
  position: relative;
}

.picture {
  margin: 0;
  padding-right: 0;
  /* border-radius: 5px; */
  display: flex;
  max-width: 70%;
}

.badge1 {
  max-width: 30%;
}

.about-me {
  text-align: left;
  padding-top: 5px;
}

.w-beach {
  box-shadow: 0px 0px 9px 5px grey;
}

@media screen and (max-width: 725px) {
  .w-beach {
    display: none;
  }
  .picture {
    padding-top: 55px;
  }
  h1 {
    padding-top: 55px;
  }
  .recommendations {
    text-align: left;
  }
  .navbar-collapse {
    display: flex;
    justify-content: center;
  }
  .about-me {
    padding-top: 60px;
  }
}


.footer {
  font-size: small;
  color: #102946;
  
}

.footer-links {
  font-size: x-large;
  color: #102946;
}

.charity-title {
  font-size: smaller;
  color: #102946;
  padding-top: 1rem;
}

.foot-bg {
  background-color: lightgrey; 
  border-top: solid #102946;
  border-top-width: 32px;
  box-shadow: 0px 0px 19px 8px grey;

}

a {
  color: #102946;
  text-decoration: none;
}
.flag {
  margin-bottom: 16px;
}

.cursor {
  cursor: pointer;
}
